___CSS_LOADER_EXPORT___.push([module.id, `.grid {
    display: grid;
    grid-template-columns: repeat(10, 40px);
    grid-template-rows: repeat(10, 40px);
    gap: 2px;
    margin: 20px;
    border: 2px solid #333;
}

.tile {
    width: 40px;
    height: 40px;
    border: 1px solid #666;
    background-color: #e0e0e0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    cursor: pointer;
    transition: background-color 0.2s;
    user-select: none;
}

.opp-tile {
    width: 40px;
    height: 40px;
    border: 1px solid #666;
    background-color: #e0e0e0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.opp-tile:hover {
    background-color: red;
    text-decoration: none;
}

.tile.selected-valid {
    background-color: #90EE90;
    border: 2px solid #4CAF50;
}

.tile.selected-invalid {
    background-color: #FFB6C6;
    border: 2px solid #ff0000;
}

.tile.preview-valid {
    background-color: #D4F1D4;
    border: 2px dashed #4CAF50;
    opacity: 0.7;
}

.gameboards {
    display: flex;
    justify-content: space-around;
}

.board-container {
    text-align: center;
}

.board-title {
    font-weight: bold;
    margin-bottom: 10px;
}

.placement-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

#placement-instructions {
    text-align: center;
    margin-bottom: 20px;
}

#placement-instructions h2 {
    margin-bottom: 10px;
}

#placement-instructions p {
    margin: 5px 0;
}

#placement-instructions button {
    margin: 10px 5px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
}

#placement-instructions button:hover {
    background-color: #45a049;
}

#clear-selection {
    background-color: #f44336;
}

#clear-selection:hover {
    background-color: #da190b;
}

.hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uCAAuC;IACvC,oCAAoC;IACpC,QAAQ;IACR,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,eAAe;IACf,iCAAiC;IACjC,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,eAAe;IACf,iCAAiC;AACrC;;AAEA;IACI,qBAAqB;IACrB,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,0BAA0B;IAC1B,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;AACjB","sourcesContent":[".grid {\n    display: grid;\n    grid-template-columns: repeat(10, 40px);\n    grid-template-rows: repeat(10, 40px);\n    gap: 2px;\n    margin: 20px;\n    border: 2px solid #333;\n}\n\n.tile {\n    width: 40px;\n    height: 40px;\n    border: 1px solid #666;\n    background-color: #e0e0e0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 10px;\n    cursor: pointer;\n    transition: background-color 0.2s;\n    user-select: none;\n}\n\n.opp-tile {\n    width: 40px;\n    height: 40px;\n    border: 1px solid #666;\n    background-color: #e0e0e0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 10px;\n    cursor: pointer;\n    transition: background-color 0.2s;\n}\n\n.opp-tile:hover {\n    background-color: red;\n    text-decoration: none;\n}\n\n.tile.selected-valid {\n    background-color: #90EE90;\n    border: 2px solid #4CAF50;\n}\n\n.tile.selected-invalid {\n   